<template>
  <div class="BookCatalogue">
    <div class="fiexd">
      <van-nav-bar title="目录" left-arrow @click-left="onClickLeft" />
      <div class="cata-top">
        <div class="cata-img">
          <img class="auto-img" :src="CatalogBook.coverImage" alt="" />
        </div>
        <div class="cata-text">
          <div class="cata-title">{{ CatalogBook.title }}</div>
          <div class="cata-name">{{ CatalogBook.author }}</div>
        </div>
      </div>
      <div class="chapter">
        <div class="chapter-count">共{{ CatalogData.length }}章</div>
        <div class="chapter-order">
          倒序
          <van-icon name="arrow-up" />
        </div>
      </div>
    </div>
    <div class="cata-list">
      <div class="cata-box" @click="getSuid(list.secId,index)" v-for="(list, index) in CatalogData" :key="index">
        {{ list.title }}
      <div class="suo" :class="{hide:!list.needPay}">
        <img class="auto-img" src="../assets/suo.png" alt="">
      </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BookCatalogue",
  data() {
    return {
      bookId: "",
      CatalogData: [],
      CatalogBook: [],
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },

    getSuid(sid,index){
      this.$router.push({name:'ReadContent',params:{id:this.bookId,sid,index}})
    },

  async getCatalogData() {
   await this.axios.get("https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=" +this.bookId).then(re => {
          this.CatalogData = re.catalog;
          this.CatalogBook = re.book;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    this.bookId = this.$route.params.id;
    this.getCatalogData();
  },
};
</script>

<style lang="scss" scoped>
.fiexd{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #fff;
}
.cata-top {
  display: flex;
  align-items: center;
  padding: 10px;
  .cata-img {
    width: 80px;
    height: 100px;
  }
  .cata-text {
    margin-left: 20px;
    .cata-title {
      font-size: 14px;
    }
    .cata-name {
      color: #979797;
    }
  }
}
.chapter {
  display: flex;
  align-items: center;
  padding: 10px 10px 0 10px;
  .chapter-order {
    margin-left: auto;
    border: 1px solid #979797;
    border-radius: 15px;
    width: 50px;
    text-align: center;
  }
}

.hide{
  display: none;
}

.cata-list {
margin-top: 214px;
  padding: 0 10px;
  .cata-box {
    padding: 10px;
    border-bottom: 1px dotted #666;
    display: flex;
    align-items: center;
    .suo{
      margin-left: auto;
      width: 10px;
      height: 10px;
    }
  }
  .cata-box:nth-child(1) {
    .suo{
      display: none;
    }
    padding: 10px 0;
    background-color: #f9f9f9;
    clear: both;
    overflow: hidden;
    // transform: scale(0.9);
    color: #979797;
  }
}
 .van-nav-bar {
  padding: 10px;
}
</style>
